<script lang="ts">
import { api, generateRandomString, listenClickExit, readCookies, writeCookie } from "../tools";
import { fade, fly } from 'svelte/transition';
import Bar from "../components/bar.svelte";
import { xpUntilNext } from '../game/utils';

const cookies = readCookies();

let href = '';
interface Personal {
	id: string;
	username: string;
	discriminator: string;
	avatar: string;
	xp: number;
	level: number;
	health: number;
	maxHealth: number;
	mana: number;
	maxMana: number;
}
let personal: Personal = {
	id: '',
	username: '',
	discriminator: '',
	avatar: '',
	xp: 0,
	level: 0,
	health: 100,
	maxHealth: 100,
	mana: 100,
	maxMana: 100
};
$: dead = personal.health <= 0;

window.addEventListener('load', async () => {
	personal = await api.get('me');

	let url = window.location.origin;
	url = url.replace('://', '%3A%2F%2F');
	href = `https://discord.com/api/oauth2/authorize?client_id=738322654385406003&redirect_uri=${url}%2Fauth%2Flogin&response_type=code&scope=identify`;
	const randomString = generateRandomString(21);
	writeCookie('oauthState', randomString);
	href += `&state=${btoa(randomString)}`;
});

let opened = false;
async function open() {
	if(opened) return;
	opened = true;
	// refresh
	personal = await api.get('me');
	listenClickExit(this, () => opened = false);
}
</script>

<div class="login" class:opened class:dead on:click={open}>
	{#if !cookies.token || !personal.id}
		<a href="{href}">
			<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-user"><path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"/><circle cx="12" cy="7" r="4"/></svg>
		</a>
	{:else}
		{#await personal}
			loading...
		{:then personal} 
			<img src="https://cdn.discordapp.com/avatars/{personal.id}/{personal.avatar}.png?size=128" alt="avatar">
			{#if opened}
				<div class="username" in:fly={{y: 50, duration: 200, delay: 200}} out:fade>{personal.username}#{personal.discriminator}</div>
				<div class="level" in:fly={{y: 50, duration: 200, delay: 400}} out:fade>level {personal.level}</div>
				<div class="bar" in:fly={{y: 50, duration: 200, delay: 600}} out:fade>
					<Bar max={personal.maxHealth} value={personal.health} color="#c92845" bgColor="#7a414c">
						pv
					</Bar>
				</div>
				<div class="bar" in:fly={{y: 50, duration: 200, delay: 800}} out:fade>
					<Bar max={personal.maxMana} value={personal.mana} color="#2224aa" bgColor="#4d4e8e">
						mp
					</Bar>
				</div>
				<div class="bar" in:fly={{y: 50, duration: 200, delay: 1000}} out:fade>
					<Bar max={xpUntilNext(personal.level)} value={personal.xp} color="#eac629" bgColor="#998d5b">
						xp
					</Bar>
				</div>
			{/if}
		{/await}
	{/if}
</div>

<style>
.login {
	--pfp-size: var(--parent-size);
	z-index: 5;
	cursor: pointer;
	width: var(--pfp-size);
	height: var(--pfp-size);
	border-radius: var(--pfp-size);
	background-color: var(--border-color);
	transition: width, height, border-radius, padding, .35s ease-in-out;
	overflow: hidden;
}
.login.opened {
	--pfp-size: calc(var(--parent-size) * 1.5);
	width: 15rem;
	transform: translateX(calc(-100% + var(--parent-size)));
	height: 18rem;
	padding: 1rem;
	border-radius: 1rem;
	cursor: auto;
}
.login.dead {
	filter: grayscale(50%);
	background-image: url('../images/tombstone.png');
	background-size: 30%;
	background-position: right 3% top 3%;
	background-repeat: no-repeat;
}
.login img {
	display: block;
	width: var(--pfp-size);
	height: var(--pfp-size);
	border-radius: 100%;
	border: .2em solid var(--border-color);
	margin: auto;
	transition: width, height, .35s ease-in-out;
}
.login a {
	color: var(--text-color);
}
.login a svg {
	margin: 10%;
	height: 80%;
	width: 80%;
}
.username, .level {
	text-align: center;
}
.level {
	font-weight: 600;
}
.bar {
	font-size: 1.3rem;
	position: relative;
	margin: .5em 0;
	border-radius: .5em;
	overflow: hidden;
}
</style>
