<script lang="ts">
import { spring } from 'svelte/motion';

let coords = spring({ x: -100, y: -100 }, {
	stiffness: 0.1,
	damping: 0.25
});
let size = spring(10, {
	stiffness: 0.1,
	damping: 0.25
});
const handleOver = (e) => {
	const currentCursor = getComputedStyle(e.target).cursor;
	if (currentCursor !== 'auto') {
		size.set(20);
	} else {
		size.set(10);
	}
};

const isTouchscreen = 'ontouchstart' in window || navigator.maxTouchPoints;
if (!isTouchscreen) {
	window.addEventListener('mousemove', e => coords.set({ x: e.clientX, y: e.clientY }));
	window.addEventListener('mouseover', handleOver);
}
</script>

<div class="box" style="left:{$coords.x}px;top:{$coords.y}px;font-size:{$size}px;" />

<style>
.box {
	top: -2.5rem;
	left: -2.5rem;
	z-index: 9999;
	position: fixed;
	pointer-events: none;
	width: 5em;
	height: 5em;
	border-radius: 50%;
	border: solid .1em var(--font-color);
	opacity: 1;
	transform: translate(-50%, -50%);
}
</style>
