<script lang="ts">
import Login from "./auth/login.svelte";
import MouseFollower from "./components/mouse-follower.svelte";
import Logo from "./logo.svelte";
import Farms from "./sections/farms.svelte";

window.addEventListener('load', () => {
	const scrollSection = document.querySelector(".scroll-section");
	scrollSection.addEventListener('wheel', (e: WheelEvent) => {
		const way = e.deltaY > 0 ? 1 : -1;
		const scrollSection = document.querySelector(".scroll-section");
		scrollSection.scrollBy(window.innerWidth * way, 0);
	});
});
</script>

<svelte:head>
	<title>Merasia</title>
</svelte:head>

<header>
	<Logo class="logo"/>
	<Login/>
</header>
<div class="scroll-section">
	<Farms/>
	<Farms/>
	<Farms/>
	<Farms/>
	<Farms/>
	<Farms/>
</div>
<MouseFollower />


