<script lantg="ts">
	export let max;
	export let value;
	export let color;
	export let bgColor;
	$: percentage = (value / max) * 100;
	let mode = true;
</script>

<div class="container" style="background-color: {bgColor};" on:click={() => mode = !mode}>
	<div class="progress-bar" style="width: {percentage > 0 ? percentage : 0}%; background-color:{color}"></div>
	<span class="text">
		{#if mode}
			{value === max ? value : `${value}/${max}`} <slot></slot>
		{:else}
			{Math.round(percentage)}% <slot></slot>
		{/if}
	</span>
</div>

<style>
	.container {
		position: relative;
		width: 100%;
		height: 1em;
		text-align: center;
	}
	.progress-bar {
		height: 100%;
	}
	.text {
		margin: .15em;
		font-size: .7em;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		color: #fff;
		text-shadow: 0 0 .2em #000;
	}
</style>