export const api = {
    async get(path) {
        const res = await fetch('api/' + path);
        return await res.json();
    }
};
// a function that generates a random string
export function generateRandomString(length) {
    let randomString = '';
    const randomNumber = Math.floor(Math.random() * 10);
    for (let i = 0; i < length + randomNumber; i++) {
        randomString += String.fromCharCode(33 + Math.floor(Math.random() * 94));
    }
    return randomString;
}
// a function that reads cookies and parses them into an object
export function readCookies() {
    const cookies = {};
    document.cookie.split('; ').forEach((cookie) => {
        const [key, value] = cookie.split('=');
        cookies[key] = value;
    });
    return cookies;
}
export function writeCookie(name, value) {
    document.cookie = `${name}=${value}`;
}
export function listenClickExit(element, callback) {
    document.addEventListener('click', (event) => {
        if (element.contains(event.target)) {
            return;
        }
        callback();
    });
}
